import React from 'react';
import { StyleSheet, FlatList, Text, View, Image, TouchableOpacity, Button } from 'react-native';

import { COLORS } from '../constants/Colors';


const LegendPhoto = props => {
  const text = props.text;

  return (
    <View style={styles.viewLegendPhoto}>
      <Text style={styles.textLegendPhoto}>{text}</Text>
    </View>
  )
}

class TouchablePhoto extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      legend: false,
    };
    this.source = props.source;

    this.handleOnMouseEnter = this.handleOnMouseEnter.bind(this);
    this.handleOnMouseLeave = this.handleOnMouseLeave.bind(this);
    this.legend = <LegendPhoto text={props.legend} />
  }

  handleOnMouseEnter() {
    this.setState({
      legend: true,
    });
  }

  handleOnMouseLeave() {
    this.setState({
      legend: false,
    });
  }

  render() {
    return (
      <TouchableOpacity
      onMouseEnter={this.handleOnMouseEnter}
      onMouseLeave={this.handleOnMouseLeave}>
        <Image
        source={this.source}
        style={styles.photoImage} />
        {this.state.legend ? this.legend : null}
      </TouchableOpacity>
    )
  }
};

function getCommonPhotos() {
  const photos = [];
  require("../photos/data.json").common.map(photo => {
    photos.push(
      <TouchablePhoto source={require("../photos/common/" + photo.path)} legend={photo.legend} />
    );
  });
  return photos;
}

const commonPhotos = getCommonPhotos();
const photosPerPage = 15;

const lengthCommonPhotos = commonPhotos.length;
const maxPage = Math.floor(lengthCommonPhotos/photosPerPage);

export default function GalleryScreen(props) {
  const [page, setPage] = React.useState(0);

  function keyArrowHandler(event) {
    if (event.key === "ArrowLeft") {
      prevPage();
    } else if (event.key === "ArrowRight") {
      nextPage();
    };
  };

  React.useEffect(() => {
    document.addEventListener('keydown', keyArrowHandler);
    return () => {document.removeEventListener('keydown', keyArrowHandler)};
  });

  const nextPage = () => {
    const newPage = (page+1)%(maxPage+1);
    setPage(newPage);
  };

  const prevPage = () => {
    const newPage = (page == 0) ? (maxPage) : page-1;
    setPage(newPage);
  };

  const renderItem = ({ item, index }) => {
    if (index >= page*photosPerPage && index < page*photosPerPage+photosPerPage)
      return item;
  };

  return (
    <View style={props.style}>
      <Text style={styles.descriptionText}>Galería</Text>
      <View style={styles.photosView}>
        <TouchableOpacity style={styles.pageButton} onPress={prevPage}>
          <Text style={{fontSize: 48, fontWeight: 'bold', color: 'white'}}>{"<"}</Text>
        </TouchableOpacity>
        <FlatList
        numColumns={5}
        data={commonPhotos}
        extraData={page}
        renderItem={renderItem} />
        <TouchableOpacity style={styles.pageButton} onPress={nextPage}>
          <Text style={{fontSize: 48, fontWeight: 'bold', color: 'white'}}>{">"}</Text>
        </TouchableOpacity>
      </View>
      <Text style={styles.descriptionText}>Página: {page+1} de {maxPage+1}</Text>
    </View>
  )
};

const styles = StyleSheet.create({
  descriptionView: {
    textAlign: 'center',
    alignItems: 'center',
    marginBottom: 16,
  },
  descriptionText: {
    color: COLORS.BodyTextColor,
    fontSize: 24,
    marginTop: 16,
  },
  photosView: {
    flexDirection: 'row',
    borderTopWidth: 48,
    borderBottomWidth: 48,
  },
  photoImage: {
    height: 200,
    width: 200,
    resizeMode: 'cover',
  },
  pageButton: {
    backgroundColor: 'black',
    paddingHorizontal: 12,
    justifyContent: 'center',
  },
  viewLegendPhoto: {
    backgroundColor: COLORS.HeaderBackgroundColor,
    paddingTop: 4,
    position: 'absolute',
    bottom: 0,
    width: '100%',
    height: '20%',
  },
  textLegendPhoto: {
    color: COLORS.HeaderTextColor,
  }
})
